export default class {
	constructor(){
		this.el = document.createElement('div');
		this.el.setAttribute('class', 'center');
		// this.el.style = 'z-index: 1; position: fixed; display: none; border: 1px solid red; max-width: 80%;';
		this.el.style = 'z-index: 1; position: fixed; display: none; max-width: 80%;';

		this.img = new Image();
		this.img.style = 'max-width: 100%; outline: 1px solid red;';
		this.el.appendChild(this.img);

		this.closeButton = document.createElement('div');
		this.closeButton.setAttribute('class', 'close-button');
		this.el.appendChild(this.closeButton);
		this.closeButton.addEventListener('click', this.hide.bind(this));

		const cross = document.createElement('span');
		cross.innerText = 'X';
		cross.setAttribute('class', 'center');
		cross.style = 'font-size: 200%;';
		this.closeButton.appendChild(cross);

		document.body.appendChild(this.el);
	}

	show(){
		this.el.style.display = 'block';
	}

	hide(){
		this.el.style.display = 'none';
	}

	setURL(url){
		this.img.src = url;
	}
}