<script>
	import BigImage from './packets/BigImage.js';

	function init(){
		const backgrounds = [
			'https://wallpapercave.com/wp/wp2555875.png',
			'https://wallpapercave.com/wp/wp2555881.png',
			'https://data2.1freewallpapers.com/download/stars-sky-night-1920x1080.jpg',
			'https://orsted.com/-/media/WWW/Images/Corp/Campaign/SpaceSafari/space-safari-background.png',
			'https://miro.medium.com/max/2625/1*5Yaci134qndT0MaiMjTUjw.jpeg'
		];

		(async () => {
			const loop = true;
			while(loop){
				if(backgrounds.length === 0) return;
				
				if(localStorage.prevBackgroundUrl && backgrounds.length > 1){
					backgrounds.splice(backgrounds.indexOf(localStorage.prevBackgroundUrl), 1);
				}

				const backgroundUrl = backgrounds[~~(Math.random() * backgrounds.length)];
				try {
					await checkImage(backgroundUrl);
					localStorage.prevBackgroundUrl = backgroundUrl;
					setBackgroundImage(backgroundUrl);
					return;
				} catch (e){
					console.error(e);
					backgrounds.splice(backgrounds.indexOf(backgroundUrl), 1);
				}
			}
		})();

		function setBackgroundImage(url) {
			document.body.style['background-image'] = `url('${url}')`;
		}

		function checkImage(url){
			const img = new Image();
			img.src = url;
			return new Promise((resolve, reject) => {
				img.onerror = reject;
				img.onload = resolve;
			});
		}

		const bigImage = new BigImage();

		Array.from(document.querySelectorAll('img')).forEach(img => {
			if(img === bigImage.img) return;

			img.addEventListener('click', function(){
				bigImage.setURL(this.src);
				bigImage.show();
			});
			img.style.cursor = 'pointer';
		});
	}

	export default {
		name: 'App',
		components: {
			
		},
		mounted: init
	}
</script>

